.form {
  padding-bottom: 44px;
}

.form__data {
  margin: 0;
}

@media (min-width: 768px) {
  .form {
    padding: 10px 60px 64px;
  }
}
@media (min-width: 1200px) {
  .form {
    padding: 10px 120px 64px;
  }
}
.form-data__group-wrapper {
  border-top: 1px solid #d6d6d6;
  padding: 24px 20px 34px;
}
.form-data__group-wrapper:last-of-type {
  padding-bottom: 56px;
}

.form-data__group {
  margin: 0;
  padding: 0;
  border: none;
}

.form-data__title {
  margin-bottom: 20px;
  text-align: center;
}

.form-data__control {
  display: block;
  margin-bottom: 16px;
}

.form-data__text {
  margin-left: 16px;
}

.form-data__contacts-wrapper {
  display: grid;
  grid-template-columns: 222px 58px;
}

.form-data__field {
  font-family: "PT Sans", "Arial", "Helvetica", sans-serif;
  width: 280px;
  display: block;
  padding: 16px;
  background-color: #f2f2f2;
  border: none;
  text-transform: none;
}
.form-data__field::placeholder {
  color: #000000;
}
.form-data__field--phone::placeholder, .form-data__field--email::placeholder {
  opacity: 0.3;
}
.form-data__field--email, .form-data__field--phone {
  width: 222px;
}
.form-data__field:hover {
  background-color: #e5e5e5;
}
.form-data__field:focus {
  background-color: #ffffff;
  box-shadow: inset 0 0 0 4px #e5e5e5;
  outline: none;
}

.form-data__radio-wrapper {
  padding-top: 10px;
}

.form-data__control-radio {
  position: relative;
  display: block;
  padding-left: 62px;
}
.form-data__control-radio:not(:last-child) {
  margin-bottom: 36px;
}

.form-data__mark {
  position: absolute;
  top: -10px;
  left: 0;
  width: 46px;
  height: 46px;
  background-color: #f2f2f2;
}
.form-data__mark--radio {
  border-radius: 50%;
}
.form-data__mark--radio:hover {
  background-color: #e5e5e5;
}
.form-data__mark--radio:active {
  background-color: #d5d5d5;
}
.form-data__mark--check:hover {
  background-color: #e5e5e5;
}
.form-data__mark--check:active {
  background-color: #d5d5d5;
}

.form-data__radio:checked + .form-data__mark--radio::before {
  position: absolute;
  top: 15px;
  left: 15px;
  width: 16px;
  height: 16px;
  background-color: #333333;
  border-radius: 50%;
  content: "";
}

.form-data__radio:checked + .form-data__mark--radio:active::before {
  position: absolute;
  top: 15px;
  left: 15px;
  width: 16px;
  height: 16px;
  background-color: rgba(51, 51, 51, 0.3);
  border-radius: 50%;
  content: "";
}

.form-data__check-wrapper {
  padding-top: 10px;
}

.form-data__control-check {
  position: relative;
  display: block;
  padding-left: 62px;
}
.form-data__control-check:not(:last-child) {
  margin-bottom: 36px;
}

.form-data__check:checked + .form-data__mark--check {
  background-image: url("../../img/icon/icon-tick.svg");
  background-position: center;
  background-repeat: no-repeat;
}

.form-data__check:checked + .form-data__mark--check:active {
  opacity: 0.3;
  background-color: #979797;
}

.form-data__phone-logo {
  width: 58px;
  height: 58px;
  background-color: #e6e6e6;
  background-image: url("../../img/icon/icon-phone.svg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
}

.form-data__email-logo {
  width: 58px;
  height: 58px;
  background-color: #e6e6e6;
  background-image: url("../../img/icon/icon-mail.svg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
}

.form-data__big-text {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
}

.form-data__textarea {
  font-family: "PT Sans", "Arial", "Helvetica", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  margin-bottom: 48px;
  padding: 16px;
  background-color: #f2f2f2;
  border: none;
  width: 100%;
  height: 150px;
}
.form-data__textarea::placeholder {
  opacity: 0.3;
}
.form-data__textarea:hover {
  background-color: #e5e5e5;
}
.form-data__textarea:focus {
  background-color: #ffffff;
  box-shadow: inset 0 0 0 4px #e5e5e5;
  outline: none;
}

.form-data__button-wrapper {
  padding: 0 20px;
}

.form-data__button {
  width: 100%;
}

.form-data__hint {
  float: right;
  font-size: 12px;
  line-height: 21px;
  opacity: 0.6;
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .form-data__contacts {
    display: grid;
    grid-template-columns: 280px 280px;
    gap: 0 88px;
  }

  .form-data__group-wrapper {
    position: relative;
    margin-bottom: 48px;
    padding: 0;
    border: none;
  }
  .form-data__group-wrapper:first-child::after {
    position: absolute;
    top: 0;
    right: -43px;
    width: 1px;
    height: 100%;
    background-color: #dbdbdb;
    content: "";
  }

  .form-data__title-line {
    position: relative;
    margin-bottom: 28px;
  }
  .form-data__title-line::before {
    position: absolute;
    top: 40%;
    width: 100%;
    height: 1px;
    background-color: #dbdbdb;
    content: "";
  }

  .form-data__title {
    font-size: 21px;
    line-height: 26px;
    margin-bottom: 28px;
  }
  .form-data__title--contacts {
    margin-left: -18px;
  }
  .form-data__title--line {
    position: relative;
    width: max-content;
    max-width: 410px;
    margin: 0 auto;
    padding: 0 65px;
    background-color: #ffffff;
  }

  .form-data__radio-wrapper {
    display: grid;
    grid-template-columns: repeat(2, 280px);
    gap: 0 20px;
    padding: 10px 33px 0;
  }

  .form-data__check-wrapper {
    display: grid;
    grid-template-columns: repeat(2, 224px);
    gap: 0 32px;
    padding: 10px 84px 0;
  }

  .form-data__textarea {
    margin: 0;
  }

  .form-data__button-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0;
  }

  .form-data__button {
    width: 324px;
  }
