@media (min-width: 1200px) {
  .form-data__contacts {
    grid-template-columns: 430px 402px;
    gap: 0 128px;
  }

  .form-data__group-wrapper:first-child::after {
    right: -63px;
  }

  .form-data__contacts-wrapper {
    grid-template-columns: 344px 58px;
  }

  .form-data__field {
    width: 430px;
  }
  .form-data__field--email, .form-data__field--phone {
    width: 344px;
  }

  .form-data__title-line {
    margin-bottom: 45;
  }

  .form-data__radio-wrapper {
    grid-template-columns: repeat(3, 270px);
    gap: 0 20px;
    padding: 10px 53px 0;
  }

  .form-data__check-wrapper {
    grid-template-columns: repeat(4, 172px);
    gap: 0 64px;
    padding: 10px 44px 0;
  }

  .form-data__textarea-wrapper {
    padding: 0 96px;
  }

  .form-data__button-wrapper {
    padding: 0 96px;
  }

  .form-data__button {
    width: 384px;
  }
}
.modal-container {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 1;
}
.modal-container--close {
  display: none;
}

.modal {
  position: relative;
  width: 320px;
  margin: auto;
  background-color: #ffffff;
  text-align: center;
}
.modal--error {
  display: none;
}
.modal--success {
  display: none;
}

.modal__content--error {
  padding: 45px 42px 28px;
}
.modal__content--success {
  padding: 39px 38px 30px;
}

.modal__title {
  margin-bottom: 26px;
}

.modal__button-wrapper--error {
  padding: 0 70px 35px;
}
.modal__button-wrapper--success {
  padding: 20px;
  background-color: #f2f2f2;
}

.modal__button {
  width: 100%;
}

@media (min-width: 768px) {
  .modal--error {
    width: 400px;
  }
  .modal--success {
    width: 640px;
  }

  .modal__content--error {
    padding: 50px 47px 25px;
  }
  .modal__content--success {
    padding: 53px 98px 30px;
  }

  .modal__title {
    font-size: 30px;
    line-height: 40px;
  }

  .modal__button-wrapper--error {
    padding: 0 64px 49px;
  }
  .modal__button-wrapper--success {
    padding: 44px 91px;
    background-color: #f2f2f2;
  }
}
@media (min-width: 1200px) {
  .modal__content--error {
    padding: 48px 47px 25px;
  }
  .modal__content--success {
    padding: 51px 98px 28px;
  }
}